.media-block {
  width: 49%;
}

/*media file is on the right*/
.info-wrapper__image-right .media-block {
  padding-left: 30px;
  float: right;
}

/*media file is under text*/

.media-block_text-above {
  margin-top: 30px;
}

.info-wrapper .media-block__image {
  width: 100%; // 390px
  position: relative;
}

.info-wrapper .media-block__image img {
  width: 100%;
}

.media-block__video {
  width: 100%;
  display: block;
}

.media-block__video-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
}

.media-block__iframe-wrapper {
  height: 0;
  position: relative;
  padding-bottom: 56.25%;
}

.media-block__iframe {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.media-block__video-button {
  @include elemToCenter();
  width: 58px;
  height: 58px;
  background: url('../img/video-button.png') no-repeat center;
  z-index: 5;
  border: 0;
  @include trans();
}


.media-block__video-button:hover {
  cursor: pointer;
}

.videocover {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.25);
}

.videocover:hover {
  cursor: pointer;
}

.info-wrapper__image-left .media-block{
  float: left;
  padding-right: 30px;
}

.info-wrapper__list {
  overflow: hidden;
  list-style: none;
  margin: 0;
  padding: 0;
  padding-left: 40px;
  margin-bottom: 20px;
}

.info-wrapper__list_num {
  padding-left: 21px;
  margin-bottom: 0;
}

.info-wrapper__list-item {
  position: relative;
  padding-top: 10px;
  line-height: 26px;
}

.info-wrapper__list-item::before {
  content: '';
  position: absolute;
  top: 24px;
  left: -30px;
  display: block;
  width: 14px;
  height: 1px;
  background-color: $red;
}

.info-wrapper__list-item_dark-style::before {
  background-color: $black;
}

.info-wrapper__list-item_num::before {
  display: none;
}

.info-wrapper__list-item_num .num {
  display: inline-block;
  padding-right: 7px;
  color: $red;
  font-style: normal;

}

.info-wrapper__list-item_num .num-black {
  display: inline-block;
  padding-right: 7px;
  color: $black;
  font-style: normal;

}

.info-wrapper__text-italic {
  padding: 30px;
  padding-bottom: 35px;
  margin: 25px 0;
  background-color: $grey-light2;
}

@media (max-width: 1280px) {
  .info-wrapper .media-block__image {
    width: 100%; // 285px
  }
}

@media (max-width: $m) {
  .info-wrapper__image-right .media-block {
    padding-left: 21px;
  }

  .info-wrapper__image-left .media-block {
    padding-right: 21px;
  }

  .info-wrapper__image-right .media-block__image {
    width: 100%; // 345px
  }
}

@media (max-width: $s) {
  .info-wrapper__list {
    padding-left: 30px;
  }

  .info-wrapper__list_num {
    padding-left: 0;
  }

  .info-wrapper .media-block {
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 30px;
    width: 100%;
  }

  .info-wrapper .media-block__image {
    width: 100%;
    margin: 0 auto;
  }

  .info-wrapper .media-block__image img {
    width: 100%;
  }

  .info-wrapper__text-italic {
    padding: 20px;
    padding-bottom: 25px;
    margin: 15px 0;
  }
}

@media (max-width: $xs) {
  .info-wrapper .media-block__image {
    width: 100%;
  }
}